@import "../components/variables";
@import "../components/mixin";

body{
  .contacts{
    width: 100%;
    padding-top: 25px;
    .containerParent();
    .container{
      .containerChild();
      margin-bottom: 100px;
      display: grid;
      grid-template-areas: "breadcrumb"
                           "contacts";
      grid-template-columns: 100%;
      .content__contacts__breadcrumb {
        grid-area: breadcrumb;
        ul {
          display: flex;
          li {
            a {
              font: .875em/1em @sansregular;
              color: #474747;
            }
            span {
              font: .875em/1em @sansregular;
              color: #474747;
            }
          }
        }
      }
      .content__contacts{
        grid-area: contacts;
        h5 {
          font: 2.25em/2.375em @serifitalic;
          color: @linkColor;
        }
        ul{
          li{
            margin: 15px 0;
            h6{
              margin: 5px 0;
              font: 1.25em/1.375em @sansbold;
              color: @linkColor;
            }
            p{
              font: 1.125em/1.25em @sansregular;
              color: @linkColor;
            }
          }
        }
      }
    }
  }
}